<template>
    <el-dialog title="" :visible="show" width="1000px" @close="handleClose">
        <div class="ui-query">
            <div class="ui-title">
                <span></span>
            </div>
            <table>
                <tr>
                    <td></td>
                    <td>{{detail.name}}</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>hahahah</td>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>{{detail.company_org_type}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td>{{detail.actual_capital}}</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>GB</td>
                </tr>
                <tr>
                    <td></td>
                    <td>202-01-01</td>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td></td>
                    <td>GB0001</td>
                    <td></td>
                    <td>2331232</td>
                    <td></td>
                    <td>{{detail.credit_code}}</td>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>28-1-1</td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td></td>
                    <td></td>
                    <td></td>
                    <td>10</td>
                    <td></td>
                    <td>100</td>
                </tr>
                <tr>
                    <td></td>
                    <td>www.baidu.com</td>
                    <td></td>
                    <td>12</td>
                    <td></td>
                    <td></td>
                </tr>
                <tr>
                    <td></td>
                    <td colspan="5"></td>
                </tr>
            </table>
        </div>
    </el-dialog>
</template>

<script>
    export default {
        name: "QueryPreview",
        props: ['show', 'detail'],
        methods: {
            handleClose() {
                this.$emit('update:show', false);
            }
        }
    };
</script>

<style scoped lang="less">
    @border-color: #e6e6e6;
    .ui-query {
        .ui-title {
            text-align: left;
            margin-bottom: 5px;
            span {
                padding-left: 5px;
                border-left: 2px solid #666;
            }
        }

        table {
            width: 100%;
            border-collapse:collapse;
            td {
                border: 1px solid @border-color;
                text-align: left;
                padding: 10px 0 10px 10px;
                &:nth-child(odd) {
                    background: #f8f8f8;
                    width: 130px;
                }

                &:nth-child(even) {
                    width: 170px;
                }
            }
        }
    }
</style>


